:root{--font-family: Spartan,monospace,Arial;--yellow: #efbb35;--red: #c93736;--gray-dark: #222831;--gray-light: #8892b0;--blue-transparent: #ccd6f6;--font-size-normal: 18px;--font-size-medium: 30px;--font-size-big: 41px;--font-size-x-big: 70px;--font-size-x-x-big: 85px}*,*:before,*:after{-webkit-box-sizing:border-box;box-sizing:border-box}html,body{font-family:Spartan,monospace,Arial;font-family:var(--font-family, Spartan,monospace,Arial);background-color:#222831;background-color:var(--gray-dark, #222831);color:#f2f3f5;font-size:20px;min-height:500px;min-width:480px;margin:0;-webkit-font-smoothing:antialiased}.clearfix:after,.clearfix:before{content:'';display:table;clear:both}p{line-height:1.2rem}a{color:#efbb35;color:var(--yellow, #efbb35);-webkit-transition:0.5s color ease;transition:0.5s color ease;text-decoration:none}a:hover,a:active{color:#c93736;color:var(--red, #c93736)}nav .nav,.skills,.experience,.optional-contact ul{padding-left:0;list-style:none}.content-container{max-width:1240px}@media (min-width: 1240px){.content-container{display:block;margin:auto}}.text-center{text-align:center}.text-right{text-align:right}.text-left{text-align:left}.text-justify{text-align:justify}.responsive-img{display:block;width:100%;max-width:100%;height:auto;vertical-align:middle}header{padding:15px;color:white}nav{display:-webkit-box;display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap}nav .logo{-webkit-box-flex:1;-ms-flex:1 1 auto;flex:1 1 auto}nav .nav{max-width:500px;margin:0;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-flex:3;-ms-flex:3 1 auto;flex:3 1 auto;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between}.section{min-height:50vh;padding:15px}.section-title{position:relative;display:block;position:relative;text-align:center;font-size:41px;font-size:var(--font-size-big, 41px)}.section-title>span{position:relative;display:inline-block;z-index:2;padding-left:30px;padding-right:30px;background-color:#222831;background-color:var(--gray-dark, #222831)}.section-title:before,.section-title:after{content:'';position:absolute;height:1px;width:50%;background:transparent;top:50%}.section-title:before{background-image:-webkit-gradient(linear, right top, left top, from(#efbb35), to(transparent));background-image:linear-gradient(to left, #efbb35, transparent);left:0}.section-title:after{background-image:-webkit-gradient(linear, left top, right top, from(#efbb35), to(transparent));background-image:linear-gradient(to right, #efbb35, transparent);right:0}.flex-container{display:-webkit-box;display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between}.flex-container>*{-webkit-box-flex:1;flex:1;-ms-flex-negative:1;flex-shrink:1;-ms-flex-positive:1;flex-grow:1;-ms-flex:1;-ms-flex-shrink:0;-ms-flex-grow:1}.list-style-none{margin:0;padding-left:0;list-style:none}.skills{-webkit-column-count:5;-moz-column-count:5;column-count:5;text-align:center;text-align:-webkit-center}.skills li{width:80px;margin-bottom:15px}.experience-item h3{color:#ccd6f6;color:var(--blue-transparent, #ccd6f6);margin-top:5px;margin-bottom:10px}.experience .date{color:#efbb35;color:var(--yellow, #efbb35);font-weight:300;text-align:right;font-size:0.7rem;margin-bottom:5px}.experience .experience-item li{margin-bottom:10px}.intro{position:relative;height:100vh;padding:300px 15px}.intro-paragraph{margin-left:auto;margin-right:auto;max-width:800px;opacity:0}.intro-phrase{color:#efbb35;color:var(--yellow, #efbb35)}.intro .author-name{font-size:85px;font-size:var(--font-size-x-x-big, 85px);color:white;margin:0;font-weight:300;text-shadow:0 0 0 transparent,0 0 5px #ccd6f6,0 0 10px white}.intro .author-title{font-size:85px;font-size:var(--font-size-x-x-big, 85px);color:#efbb35;color:var(--yellow, #efbb35);margin:0}.intro:before{content:attr(data-message);position:absolute;font-weight:bold;font-size:120px;top:33%;left:25%;opacity:1;-webkit-transiti